export interface TransferRequest {
  id: string
  createdAt: string
  from: string
  to: string
  priority: 'Normal' | 'High' | 'Critical'
  items: Array<{
    name: string
    batchNo: string
    requestedQty: number
    currentQty: number
    threshold: number
  }>
  status: 'Requested' | 'Approved' | 'Rejected' | 'Completed'
  notes?: string
}

export interface ReorderItem {
  id: string
  name: string
  batchNo: string
  suggestedQty: number
  reason: string
  createdAt: string
}

// Transfer Requests
export const getTransferRequests = (): TransferRequest[] => {
  if (typeof window === 'undefined') return []
  const stored = localStorage.getItem('dms_transfer_requests')
  return stored ? JSON.parse(stored) : []
}

export const saveTransferRequest = (request: TransferRequest): void => {
  if (typeof window === 'undefined') return
  const existing = getTransferRequests()
  existing.push(request)
  localStorage.setItem('dms_transfer_requests', JSON.stringify(existing))
}

// Reorder List
export const getReorderList = (): ReorderItem[] => {
  if (typeof window === 'undefined') return []
  const stored = localStorage.getItem('dms_reorder_list')
  return stored ? JSON.parse(stored) : []
}

export const addToReorderList = (item: ReorderItem): void => {
  if (typeof window === 'undefined') return
  const existing = getReorderList()
  // Remove if already exists
  const filtered = existing.filter(r => r.id !== item.id)
  filtered.push(item)
  localStorage.setItem('dms_reorder_list', JSON.stringify(filtered))
}

export const removeFromReorderList = (id: string): void => {
  if (typeof window === 'undefined') return
  const existing = getReorderList()
  const filtered = existing.filter(r => r.id !== id)
  localStorage.setItem('dms_reorder_list', JSON.stringify(filtered))
}

// CSV Export Helper
export const downloadCSV = (filename: string, rows: string[][]): void => {
  const csvContent = rows.map(row =>
    row.map(field => `"${field.replace(/"/g, '""')}"`).join(',')
  ).join('\n')

  const blob = new Blob([csvContent], { type: 'text/csv;charset=utf-8;' })
  const link = document.createElement('a')
  const url = URL.createObjectURL(blob)

  link.setAttribute('href', url)
  link.setAttribute('download', filename)
  link.style.visibility = 'hidden'
  document.body.appendChild(link)
  link.click()
  document.body.removeChild(link)
}
